@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: rgb(47, 47, 47);
  margin: 0;
}
header {
  background: rgb(61, 61, 61);
  height: 7em;
  color: white;
  margin: 0 0 1vh 0;
  text-align: justify;
  font-family: 'Source Code Pro', monospace;
}

header::after {
  content: '';
  display: inline-block;
  width: 100%;
}

header > div,
header > div::before,
header nav,
header div h1 {
  display: inline-block;
  vertical-align: middle;
}

header > div {
  height: 100%
}

header > div::before {
  content: '';
  height: 100%;
}

header > div h1 {
  font-size: 2.2em;
}

header > div h6 {
  font-size: 1em;
}

header nav a {
  padding: 0 0.6em;
  white-space: nowrap;
}

header nav a:last-child {
  padding-right: 0;
}

h1 {
  margin: -3px 0 0 1vw;
}

h6 {
  margin: -2em 0 0 1vw
}

.Header_iconlink__3W42n {
  position: relative;
  height: 2.5em;
  margin-right: 0.2em;
}

.Header_resume__273YE {
  position: relative;
  height: 2.5em;
  margin-right: 0.2em;
  background-color: rgb(231, 231, 231);
  border-radius: 4px ;
}

nav {
  float: right;
  margin: 2em 2em 0 0;
}
.accordion__section {
  display: flex;
  flex-direction: column;
}

.accordion {
  background-color: rgb(61, 61, 61);
  color: rgb(231, 231, 231);
  cursor: pointer;
  padding: 18px;
  display: flex;
  align-items: center;
  border: none;
  outline: none;
  transition: background-color 0.6s ease;
}

.accordion:hover,
.active {
  background-color: rgb(47, 47, 47);
  color: white;
}

.accordion__title {
  font-family: 'Source Code Pro', monospace;
  font-weight: bold;
  font-size: 17px;
}

.accordion__icon {
  margin-left: auto;
  transition: transform 0.6s ease;
}

.rotate {
  transform: rotate(90deg);
}

.accordion__content {
  background-color: rgb(231, 231, 231);
  color: rgb(47, 47, 47);
  overflow: hidden;
  transition: max-height 0.6s ease;
  max-height: 0;
  display: flex;
}

.accordion__text {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-weight: 400;
  font-size: 18px;
  padding: 18px;
}

.accordion__image {
  max-width: 12em;
  margin: 0.5em 0 0 0.5em;
}
